@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville|Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title a {
  color: white;
}

.appLogo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
  height: 70px;
  margin-top: 20px;
}

.scriptLogo {
  height: 70px;
  margin-top: 20px;
}

.appHeader {
  background-color: #222;
  padding: 20px;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.footerSection {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px 20px 5px 20px;
}

.footerSection a {
  border-bottom: 1px solid white;
}

.footerSection a:hover {
  border-bottom: 2px solid white;
}

.fa-github {
  color: #131418;
}

.fa-twitter {
  color: #55acee;
}

.fa-linkedin {
  color: #0077b5;
}

.fa-instagram {
  color: #df2029;
}

.fa-coffee {
  color: #4e342e;
}

.fa {
  margin: 7px;
  -webkit-transition: 0.4s all ease-in;
  transition: 0.4s all ease-in;
  text-shadow: 0px 0px 0px white;
}

.fa:hover {
  cursor: pointer;
}

.fa-github:hover,
.fa-twitter:hover,
.fa-linkedin:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.fa-arrow-up {
  -webkit-transition: 0.2s all ease-in;
  transition: 0.2s all ease-in;
}

.fa-arrow-up:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

#repo {
  margin-left: 10px;
  text-decoration: none;
  border-bottom: 2px solid #ffc600;
}

#repo:hover {
  border-bottom: 2px solid #ff8f00;
}

/* GENERALS */
* {
  font-family: "Open Sans", "Roboto", sans-serif;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: "Libre Baskerville", "Merriweather", serif;
}

.title h1 {
  font-size: 1.7em;
}

.app {
  text-align: center;
}

a {
  color: black;
}

/* Scrollbar */

body::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}

/* ARTICLES AND ARTICLE */
.list-group {
  -webkit-transition: visibility 0s, opacity 0.5s linear;
  transition: visibility 0s, opacity 0.5s linear;
}

.card-footer {
  -webkit-transition: visibility 2s, opacity 5s linear;
  transition: visibility 2s, opacity 5s linear;
}

/* IMAGE AUTORESIZE */
.img-card-top {
  max-width: 100%;
  height: auto;
}

